const galeryObject = document.querySelector('.gallery');
console.log(galeryObject);



function imagesTemplate() {
  const markup = images
    .map(obj => {
      return `
    <a href="${obj.original}">
    <img src="${obj.preview}" alt="${obj.description}" title="${obj.description}" width = "360px"
      height = "300px"/>
    </a>`;
    })
    .join('\n');

  return markup;
}

const markup = imagesTemplate();
galeryObject.insertAdjacentHTML('afterbegin', markup);

new SimpleLightbox('.gallery a', {
    captionDelay: 600,
    nav: true,
    
});




